@use "../../scss/abstracts";
.leadership {
  &__header {
    padding: 39px 15px;
    text-align: center;
    color: abstracts.$white-theme-color;
    @include abstracts.media-breakpoint(max-991) {
      padding: 35px 15px;
    }
    @include abstracts.media-breakpoint(max-767) {
      padding: 30px 15px;
    }
    @include abstracts.media-breakpoint(max-576) {
      padding: 25px 15px;
    }
    display: flex;
    justify-content: center;
    gap: 20px;
    align-items: center;
    &-subheading {
      &.transparent {
        background-image: linear-gradient(to right, #2e3844, #455161, #db7f68, #e17a60, #e47256);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      border-right: 3px solid #000;
      padding-right: 20px;
      font-size: 16px;
      text-transform: uppercase;
      font-weight: 700;
      letter-spacing: 1px;
    }
    &-heading {
      margin: 0;
      font-size: 2.813rem;
      font-weight: 400;
      line-height: 3.125rem;
      letter-spacing: -0.01em;

      @include abstracts.media-breakpoint(max-991) {
        font-size: 50px;
        line-height: 56px;
      }
      @include abstracts.media-breakpoint(max-767) {
        font-size: 45px;
        line-height: 51px;
      }
      @include abstracts.media-breakpoint(max-576) {
        font-size: 40px;
        line-height: 45px;
      }
      @include abstracts.media-breakpoint(max-480) {
        font-size: 35px;
        line-height: 40px;
      }
      strong {
        color: transparent;
        -webkit-text-stroke: 1px abstracts.$white-theme-color;
      }
    }
  }
  &__author__box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 40px 0 0;
    @include abstracts.media-breakpoint(max-767) {
      margin: 50px 0 0;
    }
    @include abstracts.media-breakpoint(max-576) {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }

    &-item {
      -ms-flex-preferred-size: 25%;
      flex-basis: 25%;
      margin: 0 0 94px;
      padding: 0 30px;
      cursor: pointer;
      @include abstracts.media-breakpoint(max-991) {
        -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
        margin: 0 0 70px;
      }
      @include abstracts.media-breakpoint(max-767) {
        padding: 0 0 15px;
        margin: 0 0 40px;
      }
      @include abstracts.media-breakpoint(max-576) {
        margin: 0 0 30px;
      }

      &-inner {
        text-align: center;
        position: relative;
        &-img {
          margin: 0 auto 16px;
          position: relative;
          width: 224px;
          @include abstracts.media-breakpoint(max-767) {
            width: 180px;
          }
          .author-img {
            width: 224px;
            height: auto;
            -webkit-box-shadow: 0 4px 10px 2px rgba(167, 169, 172, 0.5);
            box-shadow: 0 4px 10px 2px rgba(167, 169, 172, 0.5);
            -o-object-fit: cover;
            object-fit: cover;
            @include abstracts.media-breakpoint(max-767) {
              width: 180px;
              height: 180px;
            }
          }
          .linked-in-icon {
            position: absolute;
            width: 42px;
            height: 42px;
            top: 72%;
            right: 0;
            background: abstracts.$white-theme-color;
            border-radius: 100%;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
        &-content {
          .leadershipName {
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            margin: 0 0 12px;
            @include abstracts.media-breakpoint(max-767) {
              font-size: 20px;
              line-height: 26px;
            }
          }
          .leadershipTitle {
            font-size: 17px;
            font-weight: 400;
            line-height: 20px;
            font-family: "Urbanist", sans-serif;
            margin: 0;
            @include abstracts.media-breakpoint(max-767) {
              font-size: 17px;
              line-height: 20px;
            }
          }
        }
      }

      &.front {
        .leadership__author__box-item-inner {
          &-img {
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .authorLink {
            position: absolute;
            bottom: 0;
            padding: 40px;
            width: 100%;
            height: 100%;
            background: linear-gradient(to top, #2e3844 7%, transparent 100%);
            opacity: 0;
            -webkit-transition: all 300ms ease-in-out;
            -o-transition: all 300ms ease-in-out;
            transition: all 300ms ease-in-out;
            font-size: 19px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            &:hover {
              opacity: 1;
            }
            a {
              color: abstracts.$white-theme-color;
              text-transform: uppercase;
              font-weight: 700;
              letter-spacing: 0.02em;
              margin: 0 auto;
            }
          }
        }
      }
    }
  }
}
.leadership__popup-model {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  overflow: hidden;
  outline: 0;
  width: 100%;
  background-color: abstracts.$lightgray-theme-color;
  display: none;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: auto;

  &-content {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: abstracts.$white-theme-color;
    position: relative;
    z-index: 9;
    overflow: auto;
    overflow-x: hidden;
  }
  &-body {
    max-width: 956px;
    padding: 0 15px;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    @include abstracts.media-breakpoint(max-991) {
      top: 120px;
      -webkit-transform: translate(-50%, 10px);
      -ms-transform: translate(-50%, 10px);
      transform: translate(-50%, 10px);
    }
    @include abstracts.media-breakpoint(max-767) {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }
  }
  &-author-details-main {
    -ms-flex-preferred-size: 24.2%;
    flex-basis: 24.2%;
    text-align: center;
    @include abstracts.media-breakpoint(max-991) {
      -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
    }
    @include abstracts.media-breakpoint(max-767) {
      margin: 0 0 30px;
    }

    &-img-section {
      position: relative;
      margin: 0 auto 16px;
      width: 224px;
      @include abstracts.media-breakpoint(max-767) {
        width: 180px;
      }
      .author-img {
        border-radius: 100%;
        width: 224px;
        height: 224px;
        -o-object-fit: cover;
        object-fit: cover;
        @include abstracts.media-breakpoint(max-767) {
          width: 180px;
          height: 180px;
        }
      }
      .linked-in-icon {
        position: absolute;
        width: 42px;
        height: 42px;
        top: 70%;
        right: 0;
        border-radius: 100%;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .leadershipName {
      font-size: 24px;
      font-weight: 700;
      line-height: 29px;

      color: abstracts.$primary-theme-color;
      text-transform: capitalize;
      margin: 0 0 12px;
      @include abstracts.media-breakpoint(max-767) {
        font-size: 20px;
        line-height: 26px;
      }
    }
    .leadershipTitle {
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;

      color: abstracts.$primary-theme-color;
      text-transform: capitalize;
      margin: 0;
      @include abstracts.media-breakpoint(max-767) {
        font-size: 17px;
        line-height: 20px;
      }
    }
  }
  &-about-author-box {
    -ms-flex-preferred-size: 60%;
    flex-basis: 60%;
    @include abstracts.media-breakpoint(max-991) {
      -ms-flex-preferred-size: 65%;
      flex-basis: 65%;
    }
    @include abstracts.media-breakpoint(max-767) {
      text-align: center;
    }
    .about-author {
      border-bottom: 1px solid rgba(17, 21, 44, 0.5);
      padding: 0 0 35px;
      margin: 0 0 25px;
      position: relative;
      font-size: 20px;
      font-weight: 700;
      line-height: 26px;
      letter-spacing: -0.01em;

      color: abstracts.$primary-theme-color;
      text-transform: uppercase;
      @include abstracts.media-breakpoint(max-767) {
        padding: 0 0 25px;
      }
      &:hover {
        &::before {
          width: 44px;
        }
      }
      &::before {
        content: "";
        width: 34px;
        height: 9px;
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: abstracts.$secondary-theme-color;
        -webkit-transition: all 300ms ease-in-out;
        -o-transition: all 300ms ease-in-out;
        transition: all 300ms ease-in-out;
        @include abstracts.media-breakpoint(max-767) {
          left: 50%;
          -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
          transform: translateX(-50%);
        }
      }
    }
    .leadershipBio {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      color: abstracts.$primary-theme-color;
      margin: 0 0 30px;
    }
  }
  &-header {
    position: relative;

    button {
      position: absolute;
      top: 100px;
      right: 100px;
      background: transparent;
      border: none;
      width: auto;
      height: auto;

      z-index: 999;
      cursor: pointer;
      @include abstracts.media-breakpoint(max-991) {
        top: 50px;
        right: 50px;
      }
      span {
        font-size: 20px;
        font-weight: 400;
        line-height: 26px;
        color: abstracts.$primary-theme-color;
        letter-spacing: -0.01em;
        text-transform: uppercase;
      }
    }
  }
}

.leadership__popup-model.open-popup {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.leadership__join-team {
  border-top: 1px solid #000;
  padding: 40px 0 50px;
  text-align: center;

  @include abstracts.media-breakpoint(max-576) {
    padding: 30px 0 40px;
  }
  &__heading {
    font-size: 28px;
    font-weight: 400;
    line-height: 31px;
    letter-spacing: -0.01em;
    @include abstracts.media-breakpoint(max-767) {
      font-size: 24px;
      line-height: 28px;
    }
    &-span {
      font-weight: 700;
      font-size: 28px;
      line-height: 31px;
      letter-spacing: -0.01em;
      display: block;
      margin: 0 0 50px;
      @include abstracts.media-breakpoint(max-767) {
        margin: 0 0 35px;
        font-size: 24px;
        line-height: 28px;
      }
    }
  }
}
@-webkit-keyframes slideInDown {
  0% {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}
@keyframes slideInDown {
  0% {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}
